
import {
  defineComponent,
  ref,
  Ref,
  computed,
  SetupContext,
  toRefs,
  onMounted,
  PropType,
} from 'vue';

const useInput = (modelValue: Ref<string | number>, emit: SetupContext['emit']) => {
  const innerValue = computed({
    get: () => modelValue.value,
    set: (value) => emit('update:modelValue', value),
  });

  return {
    innerValue,
  };
};

const useAutoFocus = (autofocus: Ref<boolean>) => {
  const field = ref<HTMLInputElement | null>(null);

  onMounted(() => {
    if (autofocus.value && field.value) field.value.focus();
  });

  return {
    field,
  };
};

export default defineComponent({
  name: 'AppInput',
  inheritAttrs: false,

  props: {
    label: {
      type: String,
      default: '',
    },

    modelValue: {
      type: [String, Number],
      default: '',
    },

    style: {
      type: [String, Object],
      default: '',
    },

    class: {
      type: [String, Object, Array],
      default: '',
    },

    variant: {
      type: String as PropType<'primary' | 'secondary'>,
      default: 'primary',
      validator: (value: string) => ['primary', 'secondary'].includes(value),
    },

    autofocus: Boolean,
  },

  setup(props, { emit }) {
    const { modelValue, autofocus } = toRefs(props);

    return {
      ...useInput(modelValue, emit),
      ...useAutoFocus(autofocus),

      classes: props.class,
    };
  },
});
